@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed&display=swap);
*, *:before, *:after{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

html, body{
  height: 100%;
  overflow: hidden;
}

html{
  font-size: 10px;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img{
  max-width: 100%;
  object-fit: contain;
}
#root{
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
}

.header, .footer{
  flex-shrink: 0;
  padding: 1rem;
  font: 2rem 'Roboto Condensed', sans-serif;
  text-align: center;
}

.footer{
  margin: 1rem 1rem 0;
  border-top: 1px solid #ccc;
}

.gallery{
  flex: 1 0 auto;
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-content: flex-start;
}
.loader {
    color: #900;
    font-size: 90px;
    text-indent: -9999em;
    overflow: hidden;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    margin: 72px auto;
    position: relative;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-animation: load6 1.5s infinite ease, round 1.5s infinite ease;
            animation: load6 1.5s infinite ease, round 1.5s infinite ease;
}

@-webkit-keyframes load6 {
    0% {
        box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    5%,
    95% {
        box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    10%,
    59% {
        box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
    }
    20% {
        box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
    }
    38% {
        box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
    }
    100% {
        box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
}

@keyframes load6 {
    0% {
        box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    5%,
    95% {
        box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
    10%,
    59% {
        box-shadow: 0 -0.83em 0 -0.4em, -0.087em -0.825em 0 -0.42em, -0.173em -0.812em 0 -0.44em, -0.256em -0.789em 0 -0.46em, -0.297em -0.775em 0 -0.477em;
    }
    20% {
        box-shadow: 0 -0.83em 0 -0.4em, -0.338em -0.758em 0 -0.42em, -0.555em -0.617em 0 -0.44em, -0.671em -0.488em 0 -0.46em, -0.749em -0.34em 0 -0.477em;
    }
    38% {
        box-shadow: 0 -0.83em 0 -0.4em, -0.377em -0.74em 0 -0.42em, -0.645em -0.522em 0 -0.44em, -0.775em -0.297em 0 -0.46em, -0.82em -0.09em 0 -0.477em;
    }
    100% {
        box-shadow: 0 -0.83em 0 -0.4em, 0 -0.83em 0 -0.42em, 0 -0.83em 0 -0.44em, 0 -0.83em 0 -0.46em, 0 -0.83em 0 -0.477em;
    }
}

@-webkit-keyframes round {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes round {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.photo{width:229px;height:142px;margin:3rem 2rem 0;background-color:#c4c4c4;cursor:pointer}@media (max-width: 500px){.photo{width:280px;height:171px}}

.modal-window{position:fixed;top:0;right:0;bottom:0;left:0;z-index:10;background:rgba(0,0,0,0.7);display:flex;justify-content:center;align-items:center}.modal-content{position:relative;padding:3rem;-webkit-transition:width 0.5s;transition:width 0.5s;background:white;font:1.3rem/1.5rem Roboto, sans-serif;display:flex;flex-flow:column wrap}.modal-image{width:60%;flex-basis:60%;display:flex;justify-content:center}.modal-image>img{width:360px;height:240px}.modal-form{width:60%;flex-basis:40%}.modal-comments{width:40%;flex:1 0 100%;padding:1rem 2rem}@media (min-width: 500px){.modal-content{width:650px;height:500px}.modal-comments{overflow-y:auto}}@media (max-width: 500px){.modal-content{width:100%;height:100%;padding:0;overflow-y:auto}.modal-content{flex-wrap:nowrap}.modal-image,.modal-form,.modal-comments{width:100%;flex-basis:auto}.modal-form{padding:2rem;order:2}}

.modal-form{display:flex;flex-direction:column}input{width:100%;height:3rem;border-radius:3px;font:1.3rem/1.5rem Roboto, sans-serif}input[type="text"]{margin-bottom:2rem;padding:0.7rem 1.1rem;border:1px solid #ccc}input[type="text"]::-webkit-input-placeholder{color:#ccc}input[type="text"]:-ms-input-placeholder{color:#ccc}input[type="text"]::-ms-input-placeholder{color:#ccc}input[type="text"]::placeholder{color:#ccc}input[type="submit"]{background:#4997D0;border:0;outline:0;color:#fff;cursor:pointer}

.comment{margin-bottom:2rem}.comment-date{color:#999}

.close-button{position:absolute;top:2rem;right:2rem;width:21px;height:21px;cursor:pointer}.close-button:before,.close-button:after{content:' ';position:absolute;left:47%;height:100%;width:1px;background-color:#333}.close-button:before{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.close-button:after{-webkit-transform:rotate(-45deg);transform:rotate(-45deg)}

